import flatpickr from "flatpickr";
import "flatpickr/dist/flatpickr.min.css";
import iziToast from "izitoast";
import "izitoast/dist/css/iziToast.min.css";

const startButton = document.querySelector('button[data-start]');
const datePickField = document.querySelector('#datetime-picker');
const timeSelectField = {
    days: document.querySelector('[data-days]'),
    hours: document.querySelector('[data-hours]'),
    minutes: document.querySelector('[data-minutes]'),
    seconds: document.querySelector('[data-seconds]'),
}

let userSelectedDate = null;
startButton.disabled = true;

const options = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  onClose(selectedDates) {
     userSelectedDate = selectedDates[0];
      if (userSelectedDate < new Date()) {
          iziToast.error({
              title: 'Error',
              message: 'Please choose a date in the future',
          });
          startButton.disabled = true;
      } else {
          startButton.disabled = false;
      }
  },
};

flatpickr(datePickField, options);

function convertMs(ms) {
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;
  const days = Math.floor(ms / day);
  const hours = Math.floor((ms % day) / hour);
  const minutes = Math.floor(((ms % day) % hour) / minute);
  const seconds = Math.floor((((ms % day) % hour) % minute) / second);
  return { days, hours, minutes, seconds };
}

function addLeadingZero(value) {
    return String(value).padStart(2, '0');
}

startButton.addEventListener('click', () => {
    startButton.disabled = true;
    const timeStarter = setInterval(() => {
        const currentTime = new Date();
        const differenceInTime = userSelectedDate - currentTime;
        if (differenceInTime <= 0) {
            clearInterval(timeStarter);
            timeSelectField.days.textContent = "00";
            timeSelectField.hours.textContent = "00";
            timeSelectField.minutes.textContent = "00";
            timeSelectField.seconds.textContent = "00";
            return;
        }
    const { days, hours, minutes, seconds } = convertMs(differenceInTime);
    timeSelectField.days.textContent = addLeadingZero(days.toString());
    timeSelectField.hours.textContent = addLeadingZero(hours.toString());
    timeSelectField.minutes.textContent = addLeadingZero(minutes.toString());
    timeSelectField.seconds.textContent = addLeadingZero(seconds.toString());
    })
    }, 1000)
